<i18n>
{
  "en": {
    "Deposit": "Deposit",
    "unit": "￥",
    "DepositPaid": "Deposit paid",
    "deposit": "deposit",
    "Deposit": "Deposit",
    "paymentMethod": "Please select payment method",
    "chooseAccount": "Please choose to return your account.",
    "sufficient": "（not sufficient funds）",
    "firstDescribe": "To rent the battery, you need to pay a deposit.",
    "secondDescribe": "After returning the battery, you can apply for the refund of the deposit at any time, and the expected time of arrival is 0-3 working days.",
    "Recharge": "Recharge",
    "ReturnDeposit": "Return deposit",
    "RefundSuccessful": "Refund is successful!",
    "text": "The deposit has been returned to the recharge account successfully. Please check it.",
    "GotIt": "Got it",
    "Balance": "Balance",
    "WeChat": "WeChat",
    "Alipay": "Alipay"
  },
  "zh": {
    "Deposit": "押金",
    "unit": "元",
    "DepositPaid": "已交押金",
    "deposit": "押金",
    "paymentMethod": "请选择支付方式",
    "chooseAccount": "请选择退回账号",
    "sufficient": "(余额不足)",
    "firstDescribe": "租借充电宝需要先充值押金哦。",
    "secondDescribe": "归还充电宝后，可随时申请退还押金，预计到账时间为0-3个工作日。",
    "Recharge": "立即充值",
    "ReturnDeposit": "退押金",
    "RefundSuccessful": "押金退款成功",
    "text": "押金已经成功退回充值账号，请注意查收。",
    "GotIt": "我知道了",
    "Balance": "余额消费",
    "WeChat": "微信支付",
    "Alipay": "支付宝支付"
  }
}
</i18n>

<template>
  <div class="deposit">
    <head-top :title='$t("Deposit")' :go-back="true"></head-top>
    <div class="container">
      <div class="bg">
          <div class="count">
            <span class="num">
              {{count}}
              <strong>{{$t("unit")}}</strong>
            </span>
          </div>
          <div class="ispay">
            {{isPay? $t("DepositPaid") : $t("deposit")}}
          </div>
      </div>
      <div class="pay">
        <div class="tips">{{isPay? $t("chooseAccount") : $t("paymentMethod")}}</div>
        <ul>
          <li v-if="balance" v-for="(item, index) in pay" :key="item.type" @click="selectPay(item, index)" class="bomPixel">
            <span class="icon"><img :src="item.icon" alt=""></span>
            <span class="text">{{item.text}}</span>
            <span class="circle">
                <img v-show="index === activePayIndex" src="../../assets/wallet_images/click.png" alt="">
                <img v-show="index !== activePayIndex" src="../../assets/wallet_images/quan-default.png" alt="">
            </span>
          </li>
          <li v-if="!balance" v-for="(item, index) in pay" :key="item.text" @click="selectPay(item, index)" class="bomPixel">
            <span class="icon"><img :src="item.icon" alt=""></span>
            <span class="text" v-show="item.type !== 'balancepay'">{{item.text}}</span>
            <span class="text" v-show="item.type === 'balancepay'">{{item.text}} <strong>{{ $t('sufficient') }}</strong></span>
            <span class="circle">
                <img v-show="index === activePayIndex &item.type !=='balancepay'" src="../../assets/wallet_images/click.png" alt="">
                <img v-show="index !== activePayIndex &item.type !=='balancepay'" src="../../assets/wallet_images/quan-default.png" alt="">
                <img v-show="item.type ==='balancepay'" src="../../assets/wallet_images/unclick.png" alt="">
            </span>
          </li>
        </ul>
        <p class="ml">
          <img src="../../assets/wallet_images/caution.png" alt="">
          <span>{{ $t('firstDescribe') }}</span>
        </p>
        <p class="dec">
          <span>{{ $t('secondDescribe') }}</span>
        </p>
      </div>
      <div class="btn" @click="payOrRefund">
        {{isPay? $t('ReturnDeposit') : $t('Recharge')}}
      </div>
    </div>
    <transition name="fade">
      <div v-show="showSuccess" class="success">
        <div class="column">
          <img src="../../assets/scanning_images/succeed.png" alt="">
          <span>{{ $t('RefundSuccessful') }}</span>
          <p>￥ <strong>99</strong></p>
          <span class="des">{{ $t('text') }}</span>
        </div>
        <div class="btn" @click="isBack">
          {{ $t('GotIt') }}
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
export default {
  name: 'deposit',
  data () {
    return {
      count: 99,
      activePayIndex: 0,
      pay: [
        {
          icon: require('../../assets/wallet_images/alipay.png'),
          text: this.$t('Alipay'),
          type: 'Alipay'
        },
        {
          icon: require('../../assets/wallet_images/wechat.png'),
          text: this.$t('WeChat'),
          type: 'wepay'
        },
        {
          icon: require('../../assets/wallet_images/rmb.png'),
          text: this.$t('Balance'),
          type: 'balancepay'
        }
      ],
      balance: 0, // 余额是否充足
      isPay: 0, // 是否已交押金
      showSuccess: 0
    }
  },
  components: {
    headTop
  },
  created () {
    this.balance = 1
    this.isPay = 1
  },
  methods: {
    selectPay (item, index) {
      if (!this.balance && item.type === 'balancepay' && !this.isPay) {
        return false
      } else {
        this.activePayIndex = index
      }
    },
    payOrRefund () {
      if (this.isPay) {
        this.showSuccess = true
      }
    },
    isBack () {
      this.isPay = false
      this.showSuccess = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.deposit{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .bg{
      .wh(691px,284px);
      margin: 20px auto 40px auto;
      background: url('../../assets/wallet_images/bg.png');
      background-size: cover;
      padding-top: 66px;
      font-size: 32px;
      .num{
        font-size: 82px;
        position: relative;
        strong{
          font-size: 30px;
          position: absolute;
          bottom: 10px;
          right: -50px;
        }
      }
      .ispay{
        margin-top: 20px;
        font-size: 30px;
        color: #AAAAAA;
      }
    }
    .pay{
      .tips{
        .wh-l(100%, 80px);
        text-align: left;
        padding-left: 30px;
        font-size: 30px;
        color: #aaaaaa;
      }
      ul{
        margin-bottom: 10px;
        li{
          .wh-l(100%, 90px);
          position: relative;
          span{
            display: block;
            img{
              vertical-align: middle;
            }
            &.icon{
              .wh-f(15%,100%);
              img{
                .wh(44px,44px);
              }
            }
            &.text{
              .wh-f(70%,100%);
              text-align: left;
              font-size: 32px;
              strong{
                font-size: 18px;
                color: red;
              }
            }
            &.circle{
              .wh-f(15%,100%);
              img{
                .wh(30px,30px);
              }
            }
          }
        }
      }
      .ml{
        height: 50px;
        line-height: 50px;
        font-size: 18px;
        text-align: left;
        position: relative;
        color: #AAAAAA;
        img{
          height: 22px;
          margin: 0 25px 0 25px;
          position: relative;
          top: 4px;
        }
        span{
          .zoom-font(0.9);
          display: inline-block;
        }
      }
      .dec{
        height: 50px;
        line-height: 50px;
        font-size: 18px;
        text-align: left;
        position: relative;
        color: #AAAAAA;
        padding-left: 10%;
        span{
          width: 100%;
          .zoom-font(0.9);
          display: inline-block;
        }
      }
    }
  }
  .success{
    .fs-noTop;
    .wh(100%, calc(~"100% - 90px"));
    z-index: 10;
    background: #FFF;
    .column{
      .wh(700px, 660px);
      margin: 20px auto 0 auto;
      position: relative;
      border-radius: 20px;
      box-shadow: 0px 4px 25px rgba(130,130,130,0.2);
      img{
        height: 180px;
        margin: 80px auto 62px auto;
      }
      span{
        display: block;
        font-size: 30px;
        &:last-child{
          font-size: 26px;
          color: #bdbdbd;
        }
      }
      p{
        margin: 20px 0 14px 0;
        font-size: 30px;
        strong{
          font-size: 60px;
          color: #f7223b;
        }
      }
      .des{
        font-size: 26px;
        .zoom-font(0.8);
        margin-left: 20%;
      }
    }
  }
  .btn{
    border-radius: 10px;
    font-size: 30px;
    .wh-l(690px,100px);
    background: #f4da21;
    color: #585858;
    position: absolute;
    left: 50%;
    margin-left: -345px;
    bottom: 50px;
    box-shadow: 0px 4px 25px rgba(255, 219, 0, 0.6);
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
}
</style>



